	module.exports = "<div id=\"top\"></div><header id=\"main-banner\"><div id=\"header-group\"><img id=\"main-graphic\" src=\"{{mainBackgroundImg}}\" alt=\"droidcon Krak√≥w main graphic\"/><div id=\"conference-timer\"><div class=\"clock-builder-output\"><div class=\"countdown-fld\"><span>Days</span><div id=\"days\"></div></div><div class=\"countdown-fld\"><span>Hours</span><div id=\"hours\"></div></div><div class=\"countdown-fld\"><span>Minutes</span><div id=\"minutes\"></div></div><div class=\"countdown-fld\"><span>Seconds</span><div id=\"seconds\"></div></div></div></div><div id=\"main-buttons\"><a class=\"green-button\" href=\"https://goo.gl/eyw7uF\" target=\"_blank\">Buy ticket</a><a class=\"green-button\" [routerLink]=\"['agenda']\">Agenda</a><a class=\"green-button\" href=\"#contact\">Contact</a></div></div></header><section class=\"white-background\" id=\"about\"><div class=\"container\"><div class=\"row\"><div class=\"col-xs-12 section-header\"><div class=\"section-title\">About</div><div class=\"section-line\"><hr/></div></div></div><div class=\"row\"><div class=\"col-xs-12\"><p class=\"section-paragraph\">droidcon Krak√≥w is a part of global conference\nseries and a network focusing on the best of Android.<br/>Be a part of it!<br/>Join 3rd edition of droidcon Krak√≥w!</p></div></div><div class=\"row\"><div class=\"col-xs-12 col-md-6\"><div class=\"when-where\"><img src=\"{{calendarIcon}}\" alt=\"Calendar icon image\"/><p>8-9th December 2016 - Conference<br/>10th December 2016 - Hackathon</p></div></div><div class=\"col-xs-12 col-md-6\"><div class=\"when-where empty\"><img src=\"{{pinIcon}}\" alt=\"Venue icon image\"/><p>ParkInn by Radisson<br/>Monte Cassino 2 St., Krak√≥w</p></div></div></div></div></section><section class=\"light-background\" id=\"recaption\"><div class=\"container\"><div class=\"row\"><div class=\"col-xs-12 section-header\"><div class=\"section-title\">Last year<br/>impressions</div><div class=\"section-line\"><hr/></div></div></div><div class=\"row\"><div class=\"col-xs-12\"><iframe class=\"youtube-iframe\" width=\"320\" height=\"215\" src=\"https://www.youtube.com/embed/Dg-dbrP9PaM\"></iframe></div></div><div class=\"row\"><div class=\"col-xs-12\"><div class=\"section-paragraph\"><a class=\"green-button\" href=\"https://www.youtube.com/watch?v=Dg-dbrP9PaM&amp;list=PLvpKlOXx1MBMfL4llJOi0VX6LcQVP0Su0\" target=\"_blank\">See more</a></div></div></div></div></section><section class=\"green-background\" id=\"participate\"><div class=\"container\"><div class=\"row\"><div class=\"col-xs-12 section-header\"><div class=\"section-title\">Participate<br/>in 2016!</div><div class=\"section-line\"><hr/></div></div></div><div class=\"row\"><div class=\"col-xs-12 col-lg-4\"><img src=\"{{learnIcon}}\" alt=\"Learn icon\"/><h4 class=\"section-subtitle\">Come as attendee</h4><p class=\"section-paragraph\">New place, more new speakers,\ntwo days full of anroid talks\nand workshops on day three!\nYou simply must be there this year!</p></div><div class=\"col-xs-12 col-lg-4\"><img src=\"{{teachIcon}}\" alt=\"Teach icon\"/><h4 class=\"section-subtitle\">Become a speaker</h4><p class=\"section-paragraph\">Have knowledge about Android technology\nand want to share it with others?\nNothing more simple!\nSubmit your paper and become a speaker at\ndroidcon Krak√≥w 2016!</p></div><div class=\"col-xs-12 col-lg-4\"><img src=\"{{supportIcon}}\" alt=\"Support icon\"/><h4 class=\"section-subtitle\">Support us</h4><p class=\"section-paragraph\">Help us make the conference a better event\nsimply by donating, volunteering or supporting us in\nsocial media!\nContact us for more information!</p></div></div></div></section><section class=\"white-background\" id=\"speakers-preview\"><div class=\"container\"><div class=\"row\"><div class=\"col-xs-12 section-header\"><div class=\"section-title\">See our<br/>speakers!</div><div class=\"section-line\"><hr/></div></div></div><div class=\"row preview-stack\"><div class=\"col-xs-12 col-md-4\" *ngFor=\"let speaker of speakerPreview\"><div class=\"speaker-bracket\"><img class=\"speaker-photo\" src=\"{{speakerPreviewImg(speaker.imageUrl)}}\"/><span class=\"speaker-name\">{{speaker.firstName}} {{speaker.lastName}}</span><span class=\"speaker-title\">{{speaker.websiteTitle}}</span></div></div></div><div class=\"row\"><div class=\"col-xs-12\"><p class=\"section-paragraph\"><a class=\"green-button\" [routerLink]=\"['speakers']\">Full lineup</a></p></div></div></div></section><section class=\"black-background\" id=\"call-4-paper\"><div class=\"container\"><div class=\"row\"><div class=\"col-xs-12 section-header\"><div class=\"section-title\">Submit<br/>your idea</div><div class=\"section-line\"><hr/></div></div></div><div class=\"row\"><div class=\"col-xs-12\"><p class=\"section-paragraph\">Call For Papers for droidcon Krak√≥w 2016 is already closed.<br/>We are now working on the agenda which will be published soon!</p><p class=\"section-paragraph\">Hope to see you on droidconPL 2016!</p></div></div></div></section><sponsors></sponsors><section class=\"grey-background\" id=\"jobreloaded\"><div class=\"container\"><div class=\"row\"><div class=\"col-xs-12 section-header\"><div class=\"section-title\">jobreloaded career</div><div class=\"section-line\"><hr/></div></div></div><div class=\"row\"><div class=\"col-xs-12\"><p class=\"section-paragraph\">Jobreloaded is the career booster for talented IT professionals in the worldwide Android community.</p><p class=\"section-paragraph\">Find your Android job among worldwide job opportunities and save 20% on droidcon tickets (including droidcon Krak√≥w).</p></div></div><div class=\"row\"><div class=\"col-xs-12 col-lg-6\"><iframe class=\"youtube-iframe\" src=\"https://player.vimeo.com/video/176019222\" width=\"320\" height=\"215\" frameborder=\"0\" webkitallowfullscreen=\"\" mozallowfullscreen=\"\" allowfullscreen=\"\"></iframe></div><div class=\"col-xs-12 col-lg-6\"><iframe class=\"youtube-iframe\" src=\"https://player.vimeo.com/video/173017178\" width=\"320\" height=\"215\" frameborder=\"0\" webkitallowfullscreen=\"\" mozallowfullscreen=\"\" allowfullscreen=\"\"></iframe></div></div><div class=\"row\"><div class=\"col-xs-12\"><p class=\"section-paragraph\">Read more on&nbsp;<a class=\"anchor\" href=\"http://www.jobreloaded.com/\" target=\"_blank\">jobreloaded.com</a></p></div></div></div></section><partners></partners><section class=\"grey-background\" id=\"contact\"><div class=\"container\"><div class=\"row\"><div class=\"col-xs-12 section-header\"><div class=\"section-title\">Contact us</div><div class=\"section-line\"><hr/></div></div></div><div class=\"row\"><div class=\"col-xs-12\"><p class=\"section-paragraph\">If you want to know more or have any questions regarding droidcon\nKrak√≥w, don't hesitate to email us!</p></div><div class=\"col-xs-12\"><p class=\"section-paragraph\"><a class=\"green-button\" href=\"mailto:contact@droidcon.pl\" target=\"_blank\">contact@droidcon.pl</a></p></div></div></div></section>";

