	module.exports = "<section class=\"light-background\" id=\"partners\"><div class=\"container\"><div class=\"row\"><div class=\"col-xs-12 section-header\"><div class=\"section-title\">Meet our<br/>Partners</div><div class=\"section-line\"><hr/></div></div><div class=\"col-xs-12\"><p class=\"section-paragraph\">Meet our great partners that have been supporting us among the years!</p></div></div><div class=\"row row-centered\"><div class=\"col-xs-6 col-md-4 col-lg-3 col-centered\" *ngFor=\"let partner of partners\"><div class=\"partner-bracket tooltip\"><span class=\"tooltiptext\">{{partner.partnerName}}</span><a href=\"{{partner.partnerUrl}}\" target=\"_blank\"><img src=\"{{partnerLogo(partner.partnerLogo)}}\" alt=\"{{partner.partnerName}}\"/></a></div></div></div><div class=\"row\"><div class=\"col-xs-12\"><div class=\"section-paragraph\"><a class=\"green-button\" id=\"partner-contact\" href=\"mailto:contact+partner@droidcon.pl\">Become a partner</a></div></div></div></div></section>";

