			"sessionType": "Talk",
			"sessionTitle": "Clean architecture at scale",
			"sessionDescription": "Do you remember the good old days of Android development? Life was simple and easy, and you could get away with writing whatever you were able to compile. Those times are gone now... Mobile apps have grown to sizes that would scare enterprise developers from a decade ago, and you still have to provide a smooth and slick user experience!<br />This complexity can quickly get out of hand unless we think carefully about the way we architecture our apps. We might get caught in an unmaintainable code that forces us to spend more time patching and fixing than developing new features.<br />But fear not, we have found our saviour: Clean Architecture! It‚Äôs a set of best practices that help to structure code in a layered way, with the ability to test and replace each layer as needed. Could this be the silver bullet that Android needs?<br />I was given the opportunity to start coding Bumble: a greenfield project centred on user experience - no performance sacrifice was acceptable. I knew it was the right place to apply this kind of architecture. Over 1 million downloads later I want to share my story. I want to show you how good app architecture can help you to achieve a code which is readable, modular and extendable.",
